import React from 'react';
import ReactDOM from 'react-dom';

// practice of lists

let parf1 = <p><b>Grace Murray Hopper</b>(Nueva York, 9 de diciembre de 1906 - Condado de Arlington, 1 de enero de 1992) fue una cientfica de la computacin y militar estadounidense con grado de contraalmirante. Es pionera en el mundo de las ciencias de la computacin y fue la primera programadora que utiliz el Mark I. Entre las dcadas de los 50 y 60 desarroll el primer compilador para un lenguaje de programacin as como tambin propici mtodos de validacin.</p>;
let parf2 = <p>Populariz la idea de una mquina independiente de los lenguajes de programacin, lo que deriv en el desarrollo de COBOL, un lenguaje de alto nivel de programacin que an se utiliza. Hopper intent enlistarse en la marina estadounidense durante la Segunda Guerra Mundial, pero debi unirse a las reservas de la armada porque era adulta para sus 34 aos. Era conocida por sus amistades como <em>Amazing Grace</em>.</p>;

const paraf = [parf1, parf2];
const listParf = paraf.map(paraf => (paraf));

let Career = () => {
  return  <article>
  <h3>Carrera</h3>
  <ol>
    <li>World War II</li>
    <li>UNIVAC</li>
    <li>COBOL</li>
    <li>Standards</li>
  </ol>
  </article>
}

let Quote = () => {
  return <article>
    <h3>Frase</h3>
    <blockquote cite="https://es.wikiquote.org/wiki/Grace_Hopper">
      Para m la programacin es ms que un importante arte prctico. Tambin es un desafo gigantesco en los fundamentos del conocimiento
     </blockquote>
  </article>
}

ReactDOM.render(
  <main className = 'biography'>{listParf}
  <Career />
  <Quote />
  </main>, 
  document.getElementById('main')
);


